





















/*
   默认样式reset
  */
* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    /* 在X5新内核Blink中，在排版页面的时候，会主动对字体进行放大，会检测页面中的主字体，当某一块字体在我们的判定规则中，认为字号较小，并且是页面中的主要字体，就会采取主动放大的操作。然而这不是我们想要的，可以采取给最大高度解决 */
    max-height: 100000px;
}
*:before, *:after {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    max-height: 100000px;
}
textarea{
    display: table;
}
*, *:before, *:after {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {  /*Firefox3.5+*/
    -webkit-box-sizing: border-box; /*Safari3.2+*/
    -o-box-sizing: border-box; /*Opera9.6*/
    -ms-box-sizing: border-box; /*IE8*/
    box-sizing: border-box; /*W3C标准(IE9+，Safari5.1+,Chrome10.0+,Opera10.6+都符合box-sizing的w3c标准语法)*/
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    vertical-align: baseline;
}
body {
    line-height: 1.5;
    color: #333;
    overflow-x: hidden;
    /*流畅滚动*/
    -webkit-overflow-scrolling:touch;
    font-family: "Microsoft YaHei","\5B8B\4F53","Segoe UI","Lucida Grande",Helvetica,Arial,sans-serif,FreeSans,Arimo;
    max-width:7.5rem;
    font-size:14px;
    font-size:0.28rem;
    background-size:100%;
    position:relative;
    background-color:#fff;
}
html,body{
    width:100%;
    height:100%;
}
.bodyBg{
    background-color:white;
}
/*禁止长按显示菜单栏*/
a,img{
       -webkit-touch-callout:none;
}
img {
    border: none;
    vertical-align: middle;
    max-width: 100%;
}
a {
    text-decoration: none;
    color: #333;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}
input, textarea, select, button {
    outline: none;
    vertical-align: middle;
    font-size: inherit;
    font-family: inherit;
}
textarea {
    resize: none;
    overflow-y: auto;
}
input[type="submit"], input[type="reset"], input[type="button"], button {
    cursor: pointer;
    -webkit-appearance: none;
}
hr {
    height: 0;
    border-width: 1px 0 0 0;
    border-style: solid;
    margin: 0;
    color: #ddd;
}
h1, h2, h3, h4, h5, h6 {
    font-weight: normal;
    margin: 0;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
ul, ol, li {
    list-style: none;
    -webkit-padding-start: 0
}
dt, dd {
    padding: 0;
    margin: 0;
    list-style: none;
}
.hidden {
    visibility: hidden;
}
.visible {
    visibility: visible;
}
.hide {
    display: none;
}
.show {
    display: block;
}
.rt{
    float:right;
}
.lt{
    float:left;
}
.text-left{
    text-align: left;
}
.text-right{
    text-align: right;
}
.text-center{
    text-align: center;
}
.container{
    margin:0 auto;
    background-size:100% 100%;
}
@media (max-width:768px){
    .container {
        width: 100%;
        overflow: hidden;
    }
}
/*遮罩层*/
.popup_layer,.mask{
    position:fixed;
    left: 0;
    top:0;
    width:100%;
    height:100%;
    text-align:center;
    background:rgba(0,0,0,0.6);
    background:#000\9;/*CSS Hack,只能对ie9以下浏览器ie6,ie7,ie8有效，否侧ie10,FF,Cherome会失去透明效果*/
    filter:Alpha(opacity=60);/*只对ie7,ie8有效*/
    z-index:9;
}
.flex-box{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
        align-items: center;
}
.box-1{
    -webkit-box-flex: 1;
    -ms-flex: 1;
        flex: 1;
    min-width: 0;
}
.media_title {
    display: block;
    font-weight: 400;
    font-size: 12px;
    width: auto;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-wrap: normal;
    word-wrap: break-word;
    word-break: break-all;
}
